export default {
	data() {
		return {
			timesList:[{time:'9:00-10:00'},{time:'11:00-8:00'},{time:'19:00-12:00'}]
		};
	},
	methods:{
		del(index){
			uni.showModal({
			    content: '确定要删除此时间段吗',
			    success: res=>{
			        if (res.confirm) {
			            this.timesList.splice(index,1)
			        }
			    }
			});
		}
	}
}
